@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Gilroy";
  font-display: swap;
  src: url("../fonts/Gilroy-Light.woff2") format("woff2"), url("../fonts/Gilroy-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Gilroy";
  font-display: swap;
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Gilroy";
  font-display: swap;
  src: url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}
/* stylelint-disable */
.h2o-overlay {
  position: fixed;
  height: 100%;
  right: 0;
  top: var(--header-height);
  bottom: 64px;
  left: 0;
  z-index: 29;
  padding: 20px 20px;
  overflow-y: scroll;
  background: rgba(var(--bg-b), 0.8);
  backdrop-filter: blur(5px);
  visibility: hidden;
  opacity: 0;
  transition: all 1s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.h2o-overlay::-webkit-scrollbar {
  display: none;
}
.h2o-overlay.active {
  display: grid;
  justify-content: center;
  align-items: center;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}

:root {
  --content-width: 1170px;
  --container-offset: 25px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --font-montserrat: "Montserrat" , sans-serif;
  --font-gilroy: "Gilroy" , sans-serif;
  --cl-a: 255, 255, 255;
  --cl-b: 0, 0, 0;
  --cl-c: 56, 114, 254;
  --cl-d: 31, 36, 42;
  --cl-e: 233, 233, 233;
  --bg-a: 255, 255, 255;
  --bg-b: 0, 0, 0;
  --bg-c: 56, 114, 254;
  --bg-d: 228, 231, 240;
  --bg-f: 175, 175, 175;
  --br-a: #fff;
  --br-a: #fff;
  --br-c: #3872fe;
  --br-d: #a1a1a1;
  --br-e: #acacac;
  --br-f: #afafaf;
}

html {
  box-sizing: border-box;
  width: 100%;
}

body {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  min-height: -webkit-fill-available;
  margin: 0 auto;
  -webkit-text-size-adjust: 100%;
}

main {
  margin: 0 auto;
  min-width: 0 !important;
}
main.no-padding {
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  line-height: initial;
  color: inherit;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

p {
  font-size: clamp(14px, 1.3559vw, 16px);
  font-family: var(--font-family);
  font-weight: 400;
  line-height: 1.4;
  color: rgba(var(--cl-d), 1);
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

label {
  display: inline-flex;
}

input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
}
input::placeholder {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

textarea {
  border: none;
  outline: none;
  resize: none;
}

input[type=number] {
  appearance: textfield;
}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.calculator-section {
  padding: 100px 0 70px;
}
.calculator-section__wrapp {
  display: flex;
  flex-direction: column;
  gap: 41px;
}
.calculator-section__title {
  padding-left: 16px;
  border-left: 11px solid var(--br-c);
  font-family: var(--font-montserrat);
  font-size: 40px;
  font-weight: 800;
  text-transform: uppercase;
  color: rgba(var(--cl-d), 1);
}
.calculator-section__form {
  border-radius: 10px;
  overflow: hidden;
}
.calculator-section__form-blocks {
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.calculator-section__form-values {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 25px;
  gap: 15px;
}
.calculator-section__form-label {
  display: flex;
  align-items: center;
  gap: 15px;
}
.calculator-section__form-label span {
  font-family: var(--font-gilroy);
  font-size: 30px;
  font-size: 26px;
  line-height: 100%;
  font-weight: 500;
}
.calculator-section__form-label p {
  font-family: var(--font-gilroy);
  font-size: 30px;
  font-size: 26px;
  line-height: 100%;
  font-weight: 300;
}
.calculator-section__form-input {
  height: 40px;
  width: 130px;
  padding: 0 10px;
  background-color: rgba(var(--bg-a), 1);
  border: 1px solid var(--br-e);
  font-family: var(--font-gilroy);
  font-size: 25px;
  line-height: 100%;
  font-weight: 400;
}
.calculator-section__form-submit {
  max-width: 440px;
  width: 100%;
  padding: 13px 0;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background-color: rgba(var(--bg-c), 1);
  border: 1px solid var(--br-c);
  border-radius: 5px;
  font-family: var(--font-gilroy);
  font-size: 25px;
  line-height: 100%;
  font-weight: 400;
  color: rgba(var(--cl-a), 1);
  transition: all 0.3s ease;
}
.calculator-section__form-submit svg {
  fill: currentColor;
  transition: 0.1s;
}
.calculator-section__form-submit:hover, .calculator-section__form-submit:active {
  color: rgba(var(--cl-c), 1);
  background-color: rgba(var(--bg-a), 1);
}
.calculator-section__block {
  display: grid;
  grid-template-columns: 360px 1fr;
  background-color: rgba(var(--bg-c), 1);
}
.calculator-section__block-title {
  padding: 27px 27px 27px 66px;
  border-right: 3px solid #fff;
  font-family: var(--font-gilroy);
  font-size: 30px;
  line-height: 100%;
  font-weight: 500;
  color: rgba(var(--cl-e), 1);
}
.calculator-section__block-content {
  padding: 15px 40px;
  display: flex;
  align-items: center;
}
.calculator-section__block-selects {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 45px;
}
.calculator-section__block-types {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.calculator-section__block-types li {
  width: 54px;
  height: 54px;
  background-color: rgba(var(--bg-f), 1);
  border: 2px solid var(--br-f);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  cursor: pointer;
}
.calculator-section__block-types li.active, .calculator-section__block-types li:hover {
  border: 2px solid var(--br-a);
  background-color: rgba(var(--bg-c), 1);
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.calculator-section__content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: rgba(var(--bg-d), 0.74);
}
.calculator-section__content-heading {
  font-family: var(--font-montserrat);
  font-size: 30px;
  line-height: 100%;
  font-weight: 800;
  text-transform: uppercase;
  color: rgba(var(--cl-d), 1);
}
.calculator-section__content-image {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calculator-section__content-left {
  padding: 40px 65px 50px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  border-right: 2px solid var(--br-d);
}
.calculator-section__content-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 40px;
}
.calculator-section__info {
  padding: 40px 20px 0;
  display: flex;
  flex-direction: column;
  gap: 27px;
}
.calculator-section__info .calculator-section__content-heading {
  margin-left: 50px;
}
.calculator-section__result {
  padding: 0 20px 50px;
  display: flex;
  flex-direction: column;
  gap: 37px;
}
.calculator-section__result-heading {
  margin-left: 30px;
  font-family: var(--font-montserrat);
  font-size: 30px;
  line-height: 100%;
  font-weight: 800;
  text-transform: uppercase;
  color: rgba(var(--cl-c), 1);
}
.calculator-section__result-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.calculator-section .select {
  position: relative;
  padding: 10px 30px;
  background-color: #fff;
  border-radius: 100px;
  user-select: none;
  transition: all 0.1s;
}
.calculator-section .select__header {
  width: 100%;
  max-width: 100%;
  cursor: pointer;
  gap: 16px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 6;
}
.calculator-section .select__header > svg {
  transition: transform 0.2s linear;
  flex-shrink: 0;
  fill: transparent;
}
.calculator-section .select__current {
  flex-grow: 1;
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  transition: all 0.3s;
  font-family: var(--font-gilroy);
  font-size: 25px;
  line-height: 120%;
  font-weight: 400;
  color: rgba(var(--cl-b), 1);
}
.calculator-section .select__body {
  right: -2px;
  position: absolute;
  z-index: 5;
  height: 0;
  top: calc(100% + 5px);
  overflow: hidden;
  transition: height 0.1s ease-in;
  border-radius: 10px;
  left: -2px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  background: rgba(var(--bg-a), 1);
}
.calculator-section .select__list {
  max-height: 500px;
  overflow-y: auto;
  padding-right: 10px;
}
.calculator-section .select__list::-webkit-scrollbar-thumb {
  background-color: #898989;
  border-radius: 8px;
}
.calculator-section .select__list::-webkit-scrollbar-track {
  background-color: #ccc;
}
.calculator-section .select__list::-webkit-scrollbar {
  width: 3px;
  border-radius: 8px;
}
.calculator-section .select__option {
  padding: 10px;
  width: 100%;
  cursor: pointer;
  transition: all 0.3s;
  font-family: var(--font-family);
  line-height: 100%;
  font-weight: 300;
  font-size: 18px;
  color: rgba(var(--cl-d), 1);
  background-color: rgba(var(--bg-a), 1);
  border-radius: 5px;
}
.calculator-section .select__option:hover {
  color: rgba(var(--cl-a), 1);
  background-color: rgba(var(--bg-c), 1);
}
.calculator-section .select__option a {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.calculator-section .select.active {
  z-index: 9;
}
.calculator-section .select.active .select__header > svg {
  transform: scaleY(-1);
}
.calculator-section .select.active .select__body {
  padding: 10px 15px 10px 10px;
}